module.exports = "import { Component, ViewEncapsulation } from '@angular/core';\r\n\r\n@Component({\r\n  selector     : 'nz-demo-badge-change',\r\n  encapsulation: ViewEncapsulation.None,\r\n  template     : `\r\n    <div>\r\n      <nz-badge [nzCount]=\"count\">\r\n        <a class=\"head-example\"></a>\r\n      </nz-badge>\r\n      <nz-button-group>\r\n        <button nz-button (click)=\"minCount()\"><i class=\"anticon anticon-minus\"></i></button>\r\n        <button nz-button (click)=\"addCount()\"><i class=\"anticon anticon-plus\"></i></button>\r\n      </nz-button-group>\r\n    </div>\r\n\r\n    <div style=\"margin-top: 10px;\">\r\n      <nz-badge [nzDot]=\"dot\">\r\n        <a class=\"head-example\"></a>\r\n      </nz-badge>\r\n      <nz-switch [(ngModel)]=\"dot\"></nz-switch>\r\n    </div>\r\n  `,\r\n  styles       : [ `\r\n    .ant-badge:not(.ant-badge-status) {\r\n      margin-right: 20px;\r\n    }\r\n\r\n    .head-example {\r\n      width: 42px;\r\n      height: 42px;\r\n      border-radius: 4px;\r\n      background: #eee;\r\n      display: inline-block;\r\n    }\r\n  ` ]\r\n})\r\nexport class NzDemoBadgeChangeComponent {\r\n\r\n  count = 5;\r\n  dot = true;\r\n\r\n  addCount(): void {\r\n    this.count++;\r\n  }\r\n\r\n  minCount(): void {\r\n    this.count--;\r\n    if (this.count < 0) {\r\n      this.count = 0;\r\n    }\r\n  }\r\n}\r\n"